@font-face {
  font-display: swap;
  font-family: Circe;
  src: local("Circe"),
       local("Circe Regular"),
       local("Circe-Regular"),
       url("../fonts/Circe/regular/Circe-Regular.woff2") format("woff2"),
       url("../fonts/Circe/regular/Circe-Regular.woff") format("woff"),
       url("../fonts/Circe/regular/Circe-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-display: swap;
  font-family: Circe;
  src: local("Circe Bold"),
       local("Circe-Bold"),
       url("../fonts/Circe/bold/Circe-Bold.woff2") format("woff2"),
       url("../fonts/Circe/bold/Circe-Bold.woff") format("woff"),
       url("../fonts/Circe/bold/Circe-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

