// Charter
@font-face {
  font-display: swap;
  font-family: Charter;
  src: local("Charter"),
       local("Charter Regular"),
       local("Charter-Regular"),
       url("../fonts/Charter/regular/Charter-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}